import { useEffect, useState, useRef } from "react";
import KeyboardedInput from 'react-touch-screen-keyboard';
import "./styles.css";

export default function App() {
  const URL = "https://api.frontendexpert.io/api/fe/wordle-words";
  const [WORD, setWORD] = useState("");
  const LENGTH = 5;
  const [currentGuess, setGuess] = useState("");
  const [guesses, addGuesses] = useState(Array(LENGTH).fill(null));
  const [classNames, setClassNames] = useState(Array(LENGTH));
  const [gameOver, setGameOver] = useState(false);
  const wins = useRef(0)
  console.log("The word is", WORD);
  useEffect(() => {
    (async () => {
      const res = await fetch(URL);
      const words = await res.json();
      const word = words[Math.floor(words.length * Math.random())];
      console.log("Random", word);
      setWORD(word);
    })();
  }, []);
  useEffect(() => {
    window.addEventListener("keydown", handleKey);
    console.log(currentGuess, "Guesses", guesses);
    if (gameOver > 0) {
      console.log("removed");
      window.removeEventListener("keydown", handleKey);
    }
    return () => window.removeEventListener("keydown", handleKey);
  }, [currentGuess, gameOver]);

  useEffect(() => {
    if (guesses[0]) {
      const newClassName = Array(5).fill("tile");
      const lastGuessIndex = guesses.findIndex((item) => item === null) - 1;
      let count = 0;
      guesses[lastGuessIndex >= 0 ? lastGuessIndex : 4]
        .split("")
        .forEach((ch, i) => {
          if (ch === WORD[i]) {
            count += 1;
            newClassName[i] += " matched";
          } else if (WORD.includes(ch)) {
            newClassName[i] += " present";
          } else {
            newClassName[i] += " notpresent";
          }
        });
      classNames[lastGuessIndex >= 0 ? lastGuessIndex : 4] = newClassName;
      setClassNames([...classNames]);
      if (count === 5) {
        console.log("You Win");
        setGameOver(1);
      } else if (lastGuessIndex < 0) {
        setGameOver(2);
      }
    }
  }, [guesses]);

  function handleRestart(){
    (async () => {
      const res = await fetch(URL);
      const words = await res.json();
      const word = words[Math.floor(words.length * Math.random())];
      console.log("Random", word);
      setWORD(word);
    })();
    setGameOver(0)
    addGuesses(Array(LENGTH).fill(null))
    setGuess("")
    setClassNames(Array(LENGTH))
  }

  function handleKey(e) {
    console.log("reue", currentGuess);
    if (e.key === "Backspace") {
      setGuess(currentGuess.slice(0, -1));
    } else if (currentGuess.length == 5 && e.key == "Enter") {
      const temp = [];
      var rep = 0;
      guesses.map((item, i) => {
        if (item == null && rep == 0) {
          rep = 1;
          temp[i] = currentGuess;
        } else {
          temp.push(item);
        }
      });
      addGuesses(temp);
      setGuess("");
    } else if (["Enter","Shift","CapsLock","Control"].some(item=>e.key===item)===false && currentGuess.length < 5) {
      console.log("key pressed",e.key)
      setGuess(currentGuess + e.key.toUpperCase());
    } 
  }

  if(gameOver ===1){
    wins.current+=1
  }
  return (
    <div className="App">
    <h2>Win: {wins.current}</h2>
      <div className="grid">
        <Box
          arr={guesses}
          currGuess={currentGuess}
          guesses={guesses}
          classNames={classNames}
        />
         <KeyboardedInput
        enabled
        type="text"
        onChange={handleKey}
        defaultKeyboard="us"
        isFirstLetterUppercase={true} // optional, default is `false`
        uppercaseAfterSpace={true} // optional, default is `false`
        isDraggable={false} // optional, default is `true`
        opacity={0.9} // optional
      />
      {currentGuess}
      </div>

      {
       (gameOver === 1 && (
  
            <div
              style={{
                color:'white',
                display: "flex",
                flexDirection:'column',
                justifyContent: "center",
                alignItems: "center",
                position: "absolute",
                margin: "auto",
                top: "0",
                bottom: "0",
                left: "0",
                right: "0",
                background: "linear-gradient(90deg, darkgreen ,green)"
              }}
            >
              <h2>You Won</h2>
              <button  onClick={handleRestart}>Play Again</button>
            </div>
           
          )) ||
          (gameOver === 2 && (
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                flexDirection: "column",
                alignItems: "center",
                position: "absolute",
                margin: "auto",
                top: "0",
                bottom: "0",
                left: "0",
                right: "0",
                background: "linear-gradient(90deg,darkred ,red)"
              }}
            >
              <h1>Game Over</h1>
              <button onClick={handleRestart}>Play Again</button>
              <br />
              <h2
                style={{
                  background: "white",
                  borderRadius: "10px",
                  padding: "2px"
                }}
              >
                Correct Word is {WORD}
              </h2>
            </div>
          ))}
    </div>
  );
}

function Box({ arr, currGuess, guesses, classNames }) {
  console.log(guesses);
  console.log("classes name", classNames);
  return arr.map((item, i) => (
    <div key={i} className="row">
      {arr.map((iten, j) => (
        <div
          key={j + 100}
          className={(classNames[i] && classNames[i][j]) ?? "tile"}
        >
          {guesses[i] && guesses[i][j]}
          {!guesses[i] &&
            ((i === 0 && currGuess[j]) || (guesses[i - 1] && currGuess[j]))}
        </div>
      ))}
    </div>
  ));
}
